<template>
    <section>
        <!---->
        <el-col :span="24"
                class="toolbar"
                style="padding-bottom: 0px;">
            <el-form :inline="true"
                     :model="sysUserFilters">
                <el-form-item label="">
                    <el-input v-model="sysUserFilters.name"
                              placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="sysUserFilters.phoneNo"
                              placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="sysUserFilters.idCardNo"
                              placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="sysUserFilters.roleType"
                               placeholder=""
                               style="width:90px"
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="sysUserFilters.status"
                               placeholder=""
                               style="width:90px"
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"
                               v-on:click="getList"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"
                               @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <!---->
        <el-table :data="sysUserList"
                  v-loading="listLoading"
                  style="width: 100%;">
            <el-table-column prop="name"
                             label="">
            </el-table-column>
            <el-table-column prop="phoneNo"
                             label="">
            </el-table-column>
            <el-table-column prop="idCardNo"
                             label="">
            </el-table-column>
            <el-table-column label="">
                <template scope="scope">
                    <span>{{scope.row.gender.name}}</span>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template scope="scope">
                    <span>{{scope.row.roleType.name}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="status"
                             label=""
                             :formatter="formatStatus">
            </el-table-column>
            <el-table-column prop="createdAt"
                             label=""
                             :formatter="formatCreatedAt">
            </el-table-column>
            <el-table-column prop="updatedAt"
                             label=""
                             :formatter="formatUpdatedAt">
            </el-table-column>
            <el-table-column label=""
                             width="180">
                <template scope="scope">
                    <el-button size="small"
                               @click="handleEdit(scope.$index, scope.row)">
                        </el-button>
                    <el-button type="danger" size="small"
                               @click="handleReset(scope.$index, scope.row)">
                        </el-button>   
                </template>
            </el-table-column>
        </el-table>
    
        <!---->
        <el-col :span="24"
                class="toolbar">
            <el-pagination layout="prev, pager, next"
                           :page-size="10"
                           :total="total"
                           @current-change="handleCurrentChange"
                           style="float:right;">
            </el-pagination>
        </el-col>
    
        <!---->
        <el-dialog title=""
                   v-model="editFormVisible"
                   :close-on-click-modal="true"
                   @close="closeDialog('edit')">
            <el-form :model="sysUserDetails"
                     label-width="100px"
                     :rules="editFormRules"
                     ref="editForm">
                <el-form-item label="ID"
                              prop="id">
                    <el-col :span="10">
                        <el-input v-model="sysUserDetails.id"
                                  auto-complete="off"
                                  :disabled="true"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="name">
                    <el-col :span="10">
                        <el-input v-model="sysUserDetails.name"
                                  auto-complete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="phphoneNo">
                    <el-col :span="10">
                        <el-input v-model="sysUserDetails.phoneNo"
                                  auto-complete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="idCardNo">
                    <el-col :span="10">
                        <el-input v-model="sysUserDetails.idCardNo"
                                  auto-complete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="roleType">
                    <el-select v-model="sysUserDetails.roleType"
                               placeholder=""
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=""
                              prop="gender">
                    <el-select v-model="sysUserDetails.gender"
                               placeholder=""
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=""
                              prop="status">
                    <el-select v-model="sysUserDetails.status"
                               placeholder=""
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                        <el-option label=""
                                   value="3"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer"
                 class="dialog-footer">
                <el-button @click.native="closeDialog('edit')"></el-button>
                <el-button type="primary"
                           @click.native="editSubmit"
                           :loading="editLoading"></el-button>
            </div>
        </el-dialog>
    
        <!---->
        <el-dialog title=""
                   v-model="addFormVisible"
                   :close-on-click-modal="true"
                   @close="closeDialog('add')">
            <el-form :model="sysUserAddForm"
                     label-width="100px"
                     :rules="editFormRules"
                     ref="addForm">
                <el-form-item label=""
                              prop="name">
                    <el-col :span="10">
                        <el-input v-model="sysUserAddForm.name"
                                  auto-complete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="phoneNo">
                    <el-col :span="10">
                        <el-input v-model="sysUserAddForm.phoneNo"
                                  auto-complete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="idCardNo">
                    <el-col :span="10">
                        <el-input v-model="sysUserAddForm.idCardNo"
                                  auto-complete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label=""
                              prop="roleType">
                    <el-select v-model="sysUserAddForm.roleType"
                               placeholder=""
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=""
                              prop="gender">
                    <el-select v-model="sysUserAddForm.gender"
                               placeholder=""
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                    </el-select>
                </el-form-item>
                <!--<el-form-item label=""
                              prop="status">
                    <el-select v-model="sysUserAddForm.status"
                               placeholder=""
                               clearable>
                        <el-option label=""
                                   value="1"></el-option>
                        <el-option label=""
                                   value="2"></el-option>
                        <el-option label=""
                                   value="3"></el-option>
                    </el-select>
                </el-form-item>-->
            </el-form>
            <div slot="footer"
                 class="dialog-footer">
                <el-button @click.native="closeDialog('add')"></el-button>
                <el-button type="primary"
                           @click.native="addSubmit"
                           :loading="addLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import {
    mapGetters
} from 'vuex'

import * as types from '../../vuex/types'

import util from '../../common/js/util'

export default {
    data() {
        return {
            editFormRules: {
                name: [
                    { required: true, message: "", trigger: 'blur' }
                ],
                phoneNo: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                idCardNo: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                gender: [
                    { required: true, message: '', trigger: 'change' }
                ],
                roleType: [
                    { required: true, message: '', trigger: 'change' },
                ],
                status: [
                    { required: true, message: '', trigger: 'change' },
                ]
            },
            editLoading: false,
            addLoading: false
        }
    },
    computed: {
        ...mapGetters([
            'sysUserList',
            'sysUserDetails',
            'sysUserFilters',
            'editFormVisible',
            'listLoading',
            'addFormVisible',
            'total',
            'sysUserAddForm'
        ])
    },
    methods: {
        formatStatus: function (row, column) {
            let str = "";
            if (row.status == "1") {
                str = "";
            }
            else if (row.status == "2") {
                str = "";
            }
            else if (row.status == "3") {
                str = "";
            }

            return str;
        },
        formatCreatedAt: function (row, column) {
            return util.formatDate.utcToLocal(row.createdAt)
        },
        formatUpdatedAt: function (row, column) {
            return util.formatDate.utcToLocal(row.updatedAt)
        },
        handleCurrentChange(val) {
            this.$store.commit(types.COM_PAGE_NUM, val);
            this.getList();
        },
        getList() {
            this.$store.dispatch('getSysUserList')
        },
        //
        handleEdit: function (index, row) {
            this.$store.dispatch('getSysUserDetails', {
                id: row.id
            })
        },
        handleAdd: function () {
            this.$store.commit(types.COM_ADD_FORM_VISIBLE, true);
        },
        handleReset:function(index,row){
           this.$confirm('?', '', {
					type: 'warning'
				}).then(()=>{
					this.$store.dispatch('resetPwd',row.phoneNo).then(()=>{
						this.$message({
                        message: '',
                        type: 'success'
                        });
					},err=>{
						this.$message.error(err.message);
					})
				})	
        },
        editSubmit: function () {
            if (this.editLoading) { return false }
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    this.$store.dispatch("editSysUser").then(res => {
                        this.editLoading = false;
                        this.getList();
                    }, err => {
                        this.editLoading = false;
                        this.$message.error(err.message);
                    })
                }
            });
        },
        addSubmit: function () {
            if (this.addLoading) { return false }
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    this.addLoading = true;
                    this.$store.dispatch("addSysUser").then(res => {
                        this.addLoading = false;
                        this.getList();
                    }, err => {
                        this.addLoading = false;
                        this.$message.error(err.message);
                    })
                }
            });
        },
        closeDialog: function (type) {
            if (type == "add") {
                this.$refs.addForm.resetFields();
                this.$store.commit(types.COM_ADD_FORM_VISIBLE, false);
            }
            else {
                this.$refs.editForm.resetFields();
                this.$store.commit(types.COM_EDIT_FORM_VISIBLE, false);
            }
        }
    },
    mounted() {
        this.getList();
    }
}
</script>

<style scoped>
.line {
    text-align: center;
}
</style>


