/**
 * @desc 
 */

import api from '../../fetch/api'

/**
 * state 
 */
const state = {
  balanceIncrease: {
    phoneNo: '',
    type: 1, // 
    amount: '',
    source: '',
    sourceNo: '',
    remark: ''
  }
}

/**
 * Getter 
 */
const getters = {
  balanceIncrease: state => state.balanceIncrease
}

/**
 * actions 
 */
const actions = {
  /**
    * @desc 
    */
  balanceIncrease({commit}, palyload) {
    return api.BalanceIncrease(state.balanceIncrease).then(res => {
    })
  }
}

export default {
  state,
  actions,
getters}



