<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item label="">
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="filters.type" placeholder="" style="width:90px" clearable>
						<el-option
								v-for="item in matchTypeList"
								:label="item.name"
								:value="item.id">
						</el-option>						
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="filters.status" placeholder="" style="width:90px" clearable>
						 <el-option label="" value="1"></el-option>
      					 <el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="filters.applyOnline" placeholder="" style="width:90px" clearable>
						 <el-option label="" :value="1"></el-option>
      					 <el-option label="" :value="0"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="10">
						<el-date-picker v-model="filters.startClosing" type="date" placeholder=""  style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="4">--</el-col>
					<el-col :span="10">
						<el-date-picker v-model="filters.endClosing" type="date" placeholder=""  style="width: 100%;"></el-date-picker>
					</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getList"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<!---->
		<el-table :data="matchList"  v-loading="listLoading" style="width: 100%;">
			<!--<el-table-column type="selection" width="55">
			</el-table-column>-->
			<el-table-column label="">
				<template scope="scope">
						<span>{{scope.row.matchConfig.name}}</span>
				</template>
			</el-table-column>
			<el-table-column label="">
				<template scope="scope">
					<span>{{scope.row.matchConfig.Type.name}}-{{scope.row.matchConfig.subType.name}}</span>
				</template>
			</el-table-column>		
			<el-table-column prop="status" label="" :formatter="formatStatus">
			</el-table-column>
			<el-table-column prop="closingDatetime" label="" :formatter="formatEndDate">
			</el-table-column>
			<el-table-column prop="perHand" label="()">
			</el-table-column>
			<el-table-column prop="openingDatetime" label="" :formatter="formatSatrtDate">
			</el-table-column>
			 <el-table-column prop="applyOnline" label="" :formatter="formatApplyOnline">
            </el-table-column>
			<el-table-column label="" width="220">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">
						</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.row.id)" ></el-button>
					<el-button type="warning" size="small" @click="handleClose(scope.row.id)" v-if="scope.row.status!='2'"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<!--<el-button type="danger" :disabled="this.sels.length===0"></el-button>-->
			<el-pagination layout="prev, pager, next" :page-size="10" :total="total"  
				@current-change="handleCurrentChange" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="true" @close="closeDialog('edit')">
			<el-form :model="matchDetails" label-width="110px" :rules="editFormRules" ref="editForm">
				<el-form-item label="ID" prop="id">
					<el-col :span="5">
						<el-input v-model="matchDetails.id"  auto-complete="off" :disabled="true"></el-input>
					</el-col>				
				</el-form-item>
				<el-form-item label="" prop="matchConfigId">
					<el-select v-model="matchDetails.matchConfigId" placeholder="" clearable>
						<el-option
								v-for="item in matchConfigList"
								:label="item.name"
								:value="item.id.toString()">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="status">
					<el-select v-model="matchDetails.status" placeholder="" clearable>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="closingDatetime">
					<el-date-picker v-model="matchDetails.closingDatetime" type="datetime" format="yyyy-MM-dd HH:mm" placeholder=""  style="width: 40%;"></el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="openingDatetime">
					<el-date-picker v-model="matchDetails.openingDatetime" type="datetime" format="yyyy-MM-dd HH:mm" placeholder=""  style="width: 40%;"></el-date-picker>
				</el-form-item>
				<el-form-item label="()" prop="perHand">
					<el-col :span="5">
						<el-input v-model.number="matchDetails.perHand"  auto-complete="off"></el-input>
					</el-col>				
				</el-form-item>
				<el-form-item label="" prop="applyOnline">
					<el-switch on-text="" off-text="" v-model="matchDetails.applyOnline"></el-switch>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="closeDialog('edit')"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
	    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="true" @close="closeDialog('add')">
			<el-form :model="addMatchForm" label-width="100px" :rules="addFormRules" ref="addMatchForm">
				<el-form-item label="" prop="matchConfigId">
					<el-select v-model="addMatchForm.matchConfigId" placeholder="" clearable>
						<el-option
								v-for="item in matchConfigList"
								:label="item.name"
								:value="item.id.toString()">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="closingDatetime">
					<el-date-picker v-model="addMatchForm.closingDatetime" type="datetime" format="yyyy-MM-dd HH:mm" placeholder=""  style="width: 40%;" :picker-options="pickerOptions1"></el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="openingDatetime">
					<el-date-picker v-model="addMatchForm.openingDatetime" type="datetime" format="yyyy-MM-dd HH:mm" placeholder=""  style="width: 40%;" :picker-options="pickerOptions1"></el-date-picker>
				</el-form-item>
				<el-form-item label="()" prop="perHand">
					<el-col :span="5">
						<el-input v-model.number="addMatchForm.perHand"  auto-complete="off"></el-input>
					</el-col>				
				</el-form-item>
				<el-form-item label="" prop="applyOnline">
					<el-switch on-text="" off-text="" v-model="addMatchForm.applyOnline"></el-switch>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="closeDialog('add')"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import {
		mapGetters
	} from 'vuex'
	import moment from 'moment'

	import * as types from '../../vuex/types'
	
	import util from '../../common/js/util'
	
	export default {
		data() {
			let that=this;

			//validator
			let closeingDateTimeValidator=(rule,value,callback)=>{
				let dateNow=moment().format('YYYY-MM-DD HH:mm');
				let openingDatetime=moment(that.addMatchForm.openingDatetime.toString()).format('YYYY-MM-DD HH:mm');
				let tmpDate=moment(value.toString()).format('YYYY-MM-DD HH:mm');
				if(tmpDate<dateNow){
					callback(new Error(''))
				}
				else{
					if(tmpDate>=openingDatetime&&openingDatetime){
						callback(new Error(''))
					}
					else{
						callback();
					}
				}
			}

			//validator
			let openingDatetimeValidator=(rule,value,callback)=>{
				let dateNow=moment().format('YYYY-MM-DD HH:mm');
				let closingDatetime=moment(that.addMatchForm.closingDatetime.toString()).format('YYYY-MM-DD HH:mm');
				let tmpDate=moment(value.toString()).format('YYYY-MM-DD HH:mm');
				if(tmpDate<dateNow){
					callback(new Error(''))
				}
				else{
					if(tmpDate<=closingDatetime&&closingDatetime){
						callback(new Error(''))
					}
					else{
						callback();
					}
				}
			}

			//validator
			let editCloseingDateTimeValidator=(rule,value,callback)=>{
				let dateNow=moment().format('YYYY-MM-DD HH:mm');
				let openingDatetime=moment(that.matchDetails.openingDatetime.toString()).format('YYYY-MM-DD HH:mm');
				let tmpDate=moment(value.toString()).format('YYYY-MM-DD HH:mm');
				if(tmpDate<dateNow){
					callback(new Error(''))
				}
				else{
					if(tmpDate>=openingDatetime&&openingDatetime){
						callback(new Error(''))
					}
					else{
						callback();
					}
				}
			}

			//validator
			let editOpeningDatetimeValidator=(rule,value,callback)=>{
				let dateNow=moment().format('YYYY-MM-DD HH:mm');
				let closingDatetime=moment(that.matchDetails.closingDatetime.toString()).format('YYYY-MM-DD HH:mm');
				let tmpDate=moment(value.toString()).format('YYYY-MM-DD HH:mm');
				if(tmpDate<dateNow){
					callback(new Error(''))
				}
				else{
					if(tmpDate<=closingDatetime&&closingDatetime){
						callback(new Error(''))
					}
					else{
						callback();
					}
				}
			}

			return {				
				sels: [],
				editFormRules:{
					matchConfigId:[
						{ required: true,message:"", trigger: 'change' }
					],
					status:[
						{required: true, message: '', trigger: 'change'}
					],
					openingDatetime:[
						{type:'date',required: true, message: '', trigger: 'change'},
						{validator:editOpeningDatetimeValidator,trigger:'change'}
					],
					closingDatetime:[
						{type:'date',required: true, message: '', trigger: 'change'},
						{validator:editCloseingDateTimeValidator,trigger:'change'}
					],
					perHand:[
						{ required: true, message: ''},
						{ type: 'number', message: ''}
					]
				},
				addFormRules:{
					matchConfigId:[
						{ required: true,message:"", trigger: 'change' }
					],
					openingDatetime:[
						{type:'date',required: true, message: '', trigger: 'change'},
						{validator:openingDatetimeValidator,trigger:'change'}
					],
					closingDatetime:[
						{type:'date',required: true, message: '', trigger: 'change'},
						{validator:closeingDateTimeValidator,trigger:'change'}
					],
					perHand:[
						{ required: true, message: ''},
						{ type: 'number', message: ''}
					]
				},
				pickerOptions1:{
					disabledDate:function(obj){
						let tmpDate=moment(obj.toString(),["MM-DD-YYYY", "YYYY-MM-DD"]);
						let dateNow=moment().format('YYYY-MM-DD');
						if(tmpDate<dateNow){
							return true;
						}else{
							return false;
						}
					}
				},
				editLoading:false,
				addLoading:false
			}
		},
		computed: {
			...mapGetters([
				'matchList',
				'matchTypeList',
				'matchDetails',
				'matchConfigList',
				'filters',
				'editFormVisible',
				'listLoading',
				'addFormVisible',
				'total',
				'addMatchForm',
				'userInfo'
			])
		},
		methods: {
			formatApplyOnline(row, column) {
				let str = "";
				if (row.applyOnline) {
					str = "";
				} else {
					str = "";
				}
				return str;
       		},
			formatStatus:function(row,column){
				let str="";
				if(row.status=="1"){
					str="";
				}
				else if(row.status=="2"){
					str="";
				}
				else if(row.status=="3"){
					str="";
				}

				return str;
			},
			formatSatrtDate: function(row, column) {
				return util.formatDate.utcToLocal(row.openingDatetime)
			},
			formatEndDate: function(row, column) {
				return util.formatDate.utcToLocal(row.closingDatetime)
			},
			handleCurrentChange(val) {
				this.$store.commit(types.COM_PAGE_NUM,val);
				this.getList();
			},
			getList() {				
				this.$store.dispatch('getMatchList')
				this.$store.dispatch('getAllMatchConfigs');
			},
			getMatchTypeList(){
				if(this.matchTypeList.length==0){
					this.$store.dispatch('getMatchTypeList');
				}
			},
			//
			handleEdit: function (index, row) {
				this.$store.dispatch('getMatchDetails',{
					id:row.id
				})
			},
			handleAdd:function(){
				this.$store.commit(types.COM_ADD_FORM_VISIBLE,true);	
			},
			handleDel:function(id){
				if(this.delLoading){return false}
				this.$confirm('?', '', {
					type: 'warning'
				}).then(()=>{
					this.$store.dispatch('delMatch',id).then(()=>{
						this.getList();
					},err=>{
						this.$message.error(err.message);
					})
				})			
			},
			handleClose:function(id){
				this.$confirm('?', '', {
					type: 'warning'
				}).then(()=>{
					this.$store.dispatch('closeMatch',id).then(()=>{
						this.getList();
					},err=>{
						this.$message.error(err.message);
					})
				})		
			},
			editSubmit: function () {
				if(this.editLoading){return false}
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.editLoading=true;
						this.$store.dispatch("editMatch").then(res=>{
							this.editLoading=false;
							this.getList();
						},err=>{
							this.editLoading=false;
							this.$message.error(err.message);
						})
					}
				});
			},
			addSubmit:function(){
				if(this.addLoading){return false}
				this.$refs.addMatchForm.validate((valid) => {
					if (valid) {
						this.addLoading=true;
						this.$store.dispatch("addMatch").then(res=>{
							this.addLoading=false;
							this.getList();
						},err=>{
							this.addLoading=false;
							this.$message.error(err.message);
						})
					}
				});
			},
			closeDialog:function(type){
				if(type=="add"){
					this.$refs.addMatchForm.resetFields();
					this.$store.commit(types.COM_ADD_FORM_VISIBLE,false);
				}
				else{
					this.$refs.editForm.resetFields();
					this.$store.commit(types.COM_EDIT_FORM_VISIBLE,false);
				}						
			}
		},
		mounted() {
			this.getMatchTypeList();
			this.getList();
		}
	}
</script>

<style scoped>
	.line{
		text-align:center;
	}
</style>


