/**
 * desc 
 */
import NotFound from './pages/404.vue'
import Home from './pages/home.vue'
import Main from './pages/main.vue'

/**
 * 
 */
import Login from './pages/account/login.vue'
import AccountList from './pages/account/list.vue'

/**
 * 
 */
import MatchList from './pages/match/list' // 
import MatchConfig from './pages/match/config' // 

/**
 * 
 */
import MemberList from './pages/member/list' // 
import BalanceList from './pages/member/balance' // 
import PointsList from './pages/member/point' // 
import Coupon from './pages/member/coupon' // 
import Sprit from './pages/member/sprit' // 
import SignIn from './pages/member/signIn' // 

/**
 * 
 */
import MemberLevel from './pages/settings/memberLevel' // 
import RechargeConfig from './pages/settings/recharge' // 
import ChangeAccount from './pages/settings/changeAccount' // 

/**
 * 
 */
import Campaign from './pages/operation/campaign' // 
import Recharge from './pages/operation/recharge' // 
import chipStats from './pages/operation/chipStats' // 
import Consume from './pages/operation/consume' // 

// 
import EditPassword from './pages/personal/password' // 

let routes = [
  {
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  },
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-users',
    children: [
      { path: '/campaign', component: Campaign, name: '' },
      { path: '/recharge', component: Recharge, name: '' },
      { path: '/consume', component: Consume, name: '' },
      { path: '/chipStats', component: chipStats, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-gamepad',
    children: [
      { path: '/main', component: Main, name: '', hidden: true },
      { path: '/match', component: MatchList, name: '' },
      { path: '/matchConfig', component: MatchConfig, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-user-o',
    children: [
      { path: '/member', component: MemberList, name: '' },
      { path: '/balance', component: BalanceList, name: '' },
      { path: '/points', component: PointsList, name: '' },
      { path: '/sprit', component: Sprit, name: '' },
      { path: '/coupon', component: Coupon, name: '' },
      { path: '/signIn', component: SignIn, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'fa fa-user-secret',
    children: [
      { path: '/account', component: AccountList, name: '' }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'el-icon-setting',
    children: [
      { path: '/rechargeConfig', component: RechargeConfig, name: '' },
      { path: '/memberLevel', component: MemberLevel, name: '' },
      { path: '/changeAccount', component: ChangeAccount, name: '' }
    ]
  },
  {
    path: '*',
    hidden: true,
    redirect: { path: '/404' }
  }
]

export default routes



